

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: transparent;
}

li {
    float: left;
}

.navbar{
    font-family: 'Roboto Mono', monospace;
}

li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

/* Change the link color to #111 (black) on hover */
li a:hover {
    background-color: #111;
}
.header{
    width: 100%;
    font-family: 'Roboto Mono', monospace;
    margin-bottom: -100px;
}
span{
    text-align: center;
   padding-left: 300px;
}
main{
    width: 100%;
    height: 100%;
}